const bannerText = document.querySelector('.intro__button');
const maxText = '  ';
const minText = ' ';

const bannerTextSwap = () => {
  window.addEventListener('resize', () => {
    if (innerWidth < 768) {
      bannerText.textContent = minText;
    } else {
      bannerText.textContent = maxText;
    }
  });
};

export {bannerTextSwap};
